/* Basic Styling */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  padding: 20px;
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
}

.language-buttons {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 20px;
}

.language-btn {
  background-color: #f1f1f1;
  border: none;
  padding: 10px;
  margin-right: 10px;
  cursor: pointer;
}

.hint-text {
  font-size: 14px;
  color: #333;
  margin-top: 5px;
}

.hint-link {
  font-size: 14px;
  color: blue;
  text-decoration: underline;
  cursor: pointer;
}

.language-btn:hover {
  background-color: #e1e1e1;
}

h1 {
  font-size: 24px;
  margin-bottom: 20px;
}

.input-container-ltr {
  margin-bottom: 20px;
  text-align: left;
}

.input-container-rtl {
  margin-bottom: 20px;
  text-align: right;
}

.input {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.range-input {
  width: 100%;
  margin-top: 10px;
}

.range-input.blue {
  accent-color: #3b82f6;
}

.range-input.red {
  accent-color: #ef4444;
}

.range-input.green {
  accent-color: #10b981;
}

.hint {
  cursor: help;
}

.result {
  font-size: 18px;
  font-weight: bold;
  margin-top: 20px;
}

.success-message {
  color: #10b981;
}

.surplus {
  color: #ef4444;
}
